@import "../common/styles/sass/variables.scss";

.header {
  @media screen and (max-width: 1360px){    
        width: 90px;
        padding: 0 0 0 0;    
    }
  @media screen and (max-width: 1020px){      
          height: 55px;
          width: 100%;
          z-index: 2;      
    }
  position: fixed;
  width: 320px;
  background-color: $backgroundSecondColor;
  height: 100vh;
  z-index: 1;
  top: 0;
  display: block;
  padding-top: 110px;
  padding-left: 55px;
  .toggleHolder {
    @media screen and (max-width: 1360px){     
        top: 15px;
          left: 15px;      
      }
    @media screen and (max-width: 1020px){       
          top: 15px;
            left: 15px;
        }
    position: fixed;
    top: 30px;
    left: 30px;
    z-index: 3;
    #toggle {
      height: 23px;
      width: 30px;
      position: relative;
      &::before {
        content: '';
        height: 3px;
        width: 30px;
        position: absolute;
        -webkit-transform-origin: 50% 50%;
        transform-origin: 50% 50%;
        -webkit-transition: -webkit-transform .25s;
        -webkit-transition: all .25s;
        transition: all .25s;
        top: 8px;
        background-color: #faefff; 
        transform: translate3d(0,-8px,0px);
      }
      &::after {
        content: '';
        height: 3px;
        width: 30px;
        position: absolute;
        -webkit-transform-origin: 50% 50%;
        transform-origin: 50% 50%;
        -webkit-transition: -webkit-transform .25s;
        -webkit-transition: all .25s;
        transition: all .25s;
        top: 8px;
        background-color: #faefff;
        transform: translate3d(0,8px,0);
      }
      .menuLine {
        width: 30px;
        height: 3px;
        position: absolute;
        -webkit-transition: all .25s;
        transition: all .25s;
        margin-left: 0;
        top: 8px;
        background-color: #faefff;
      }
      
    }
  }
  .topPagination {
    @media screen and (max-width: 1360px){       
        display: none;     
      }
    @media screen and (max-width: 1020px){        
          display: inline-block;
          padding-top: 10px;
          padding-right: 20px;
          padding-bottom: 0;
          float: right;        
     }
     &>div {
      display: inline-block;
      font-weight: 700;
      vertical-align: top;
    }
    position: relative;
      z-index: 2;
      padding-bottom: 30px;
      display: inline-block;
      font-weight: 700;
      vertical-align: top;
      .currentPageNum {
        width: 25px;
        position: relative;
        height: 30px;
        color: #c6646b;
      }
      .pagination {
        position: relative;
        width: 100px;
        height: 30px;
        margin-right: 8px;
        &::after{
          content: "";
          display: block;
          position: absolute;
          height: 3px;
          width: 100%;
          background-color: #faefff;
          top: 12px;
        }
      }
      .totalPageNum {
        color: #faefff;
      }
  }
  .myInfo {    
  @media screen and (max-width: 1360px){    
      display: none;    
    }
    padding: 70px 0 0;    
  }
}







 